import React, { useEffect, useState } from "react";
import { Anchor, List, Box } from "grommet";
import { findChatroomForUser, getRolePublicProfile } from "../../library/parse";

const Home = ({ initialActiveId, senderRoleName }) => {
  const [chatroom, setChatroom] = useState([]);
  const [activeRoom] = useState(initialActiveId || 0);
  const [profileLookup, setProfileLookup] = useState({});

  useEffect(() => {
    const loadChatroom = async () => {
      const chatrooms = await findChatroomForUser(senderRoleName);
      setChatroom(chatrooms);

      const roleNames = chatrooms.reduce((p, n) => {
        return p.concat(n.get("uniqueName").split("_"));
      }, []);
      const publicRoles = await getRolePublicProfile(roleNames);
      const clookup = chatrooms.reduce((p, c) => {
        const names = c
          .get("uniqueName")
          .split("_")
          .filter((e) => e !== senderRoleName);
        p[c.id] = names
          .map((n) => {
            const found = publicRoles.find(
              (pr) => pr.role && pr.role.get("name") === n
            );
            if (found) {
              return (
                found &&
                found.rolePublicProfile &&
                found.rolePublicProfile.get("name")
              );
            } else {
              return "";
            }
          })
          .join(" ");
        return p;
      }, {});
      setProfileLookup(clookup);
    };

    loadChatroom();
  }, [senderRoleName]);
  return (
    <>
      <Box fill={true} direction="row">
        <List
          width="100%"
          children={(item) => {
            const recipientName = profileLookup[item.id];
            const recipientRoleName = item
              .get("uniqueName")
              .split("_")
              .filter((e) => e !== senderRoleName);
            return (
              <Box
                pad="small"
                background={activeRoom === item.objectId ? "light-2" : "white"}
              >
                <Anchor
                  href={`/chat/r/${senderRoleName}/c/${recipientRoleName}`}
                >
                  {recipientName}
                </Anchor>
              </Box>
            );
          }}
          pad="0"
          primaryKey="name"
          data={chatroom}
        />
      </Box>
    </>
  );
};

export default Home;
