import React, { useEffect, useState } from "react";
import { Grid, Box } from "grommet";
import Chatroom from "../Chatroom";
import { getRolePublicProfile } from "../../library/parse";
import ChatroomList from "./ChatroomList";
const Home = ({ senderRoleName, recipientRoleName }) => {
  const [recipentProfile, setRecipentProfile] = useState(null);

  useEffect(() => {
    getRolePublicProfile([recipientRoleName]).then((e) => {
      e && e.length && setRecipentProfile(e[0].rolePublicProfile);
    });
  }, [recipientRoleName]);
  return (
    <>
      <Grid
        gap="small"
        pad="small"
        fill
        areas={[
          { name: "chat", start: [0, 0], end: [0, 0] },
          { name: "room", start: [1, 0], end: [1, 0] },
        ]}
        columns={["3/4", "1/4"]}
        rows={["auto"]}
      >
        <Box fill={true} gridArea="room" direction="row">
          <ChatroomList senderRoleName={senderRoleName} />
        </Box>
        <Box gridArea="chat" pad="medium" height="405px">
          {senderRoleName && (
            <Chatroom
              chatroomName={recipentProfile && recipentProfile.get("name")}
              senderRoleName={senderRoleName}
              recipientRoleName={recipientRoleName}
              publicRoom={false}
            />
          )}
        </Box>
      </Grid>
    </>
  );
};

export default Home;
